@import "~antd/dist/antd.css";
* {
  margin: 0;
  padding: 0;
}

@import url('https://fonts.googleapis.com/css2?family=Cabin:wght@400;500;700&family=Cormorant+Garamond:wght@300;400;500;700&display=swap');

.cartscreen-container{
    width: 90%;
    margin: 0 auto;
    text-align: center;
}
.cartscreen-header{
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 10% 0 2%;
}
.cart-item{
  display: flex;
  width: 100%;
  align-items: center;
}
.product-info{
  text-align: start;
}
.product-name{
  font-size: 12px;
  font-family: 'Cormorant Garamond', serif;
  font-weight: 700;
  margin: 0;
}
.color, .size{
  font-size: 8px;
  font-family: 'Cabin', sans-serif;
  font-weight: 400;
}
.displayx{
  display: block;
}
.undisplayx{
  display: none;
}
.cartsub-total{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;

}
.subtotal {
  margin: 4% 0;
  font-size: 14px;
  font-family: 'Cormorant Garamond', serif;
  font-weight: 700;
}
.totalprice{
  margin: 4% 0;
  font-size: 18px;
  font-family: 'Cabin', sans-serif;
  font-weight: 400;
}
.checkout-btn{
  width: 100%;
  padding: 10px;
  font-family: 'Cabin', sans-serif;
  font-size: 14px;
  font-weight: 700;
  margin: 5% 0;
  background-color: rgba(0, 0, 0, 1);
  color:rgba(255, 255, 255, 1);

}
.special-offer{
  padding: 10px;
  font-family: 'Cabin', sans-serif;
  font-size: 12px;
  font-weight: 700;
}
.offer-code{
  font-family: 'Cabin', sans-serif;
  font-size: 12px;
  font-weight: 400;
}
.offer-line{
  display: block;
  margin: 0 -6%;
  border: 6px solid rgba(196, 196, 196, 0.25);
}
.suggested-header{
  font-size: 16px;
  font-family: 'Cabin', sans-serif;
  font-weight: 500;
  text-align: start;
  margin: 5% 0;

}
.suggesteditem-name{
  font-size: 14px;
  font-family: 'Cormorant Garamond', serif;
  font-weight: 500;
  margin: 1% 0;
}
.suggesteditem-price{
  font-size: 14px;
  font-family: 'Cabin', sans-serif;
  font-weight: 400;
  margin: 1% 0;
}
.suggested-card{
  width: 100%;
  background-color: rgba(244, 244, 244, 1);
  border-radius: 18px;
  border: transparent;
  overflow-x: hidden;
  text-align: center;
}
.suggested-items{
  display: grid;
grid-template-columns: 1fr 1fr;
grid-gap: 10px;
  margin: 0 0 25%;
}
@media only screen and (min-width: 700px) {
    .cartscreen-container{
      width: 40%;
      margin: 0 auto;
    }
  }