@import "~antd/dist/antd.css";
* {
  margin: 0;

  padding: 0;
}
@import url('https://fonts.googleapis.com/css2?family=Cabin:wght@400;500;600;700&family=Cormorant+Garamond:wght@300;400;500;600;700&display=swap');.architectPortfolio-container {
  width: 90%;
  margin: 0 auto;
  text-align: center;
}
.architectPortfolio-header {
  width: 100%;
  margin: 10% 0;
}
.head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 75%;
  margin: 0 0 3%;
}
.architectPage-name {
  font-size: 18px;
  font-family: "Cormorant Garamond", serif;
  font-weight: 700;
  margin: 0;
  color: rgba(0, 0, 0, 1);
  text-transform: uppercase;
}
.bottom {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.sendInquiry,
.footer-sendInquiry {
  width: 85%;
  padding: 5px;
  font-size: 12px;
  background-color: rgba(0, 0, 0, 1);
  font-family: "Cabin", sans-serif;
  color: rgba(255, 255, 255, 1);
}
.thumb-up {
  width: 10%;
  margin: 0;
  align-self: flex-end;
}
.architect-introduction {
  display: flex;
  justify-content: space-between;
}
.architect-image{
  height:200px;
}
.architect-intro,
.architectDesigns-intro {
  text-align: start;
  font-size: 12px;
  font-family: "Cormorant Garamond", serif;
  font-weight: 500;
  padding: 0 0 0 15px;
  margin: 0;
  line-height: 20px;
  align-self: flex-end;
}
.architect-philosophy {
  background-color: rgba(196, 196, 196, 0.15);
  margin: 8% -6%;
}
.philosophy {
  font-size: 20px;
  font-family: "Cormorant Garamond", serif;
  font-weight: 700;
  padding: 30px 10px;
}
.architect-designs {
}
.architectDesigns-firstDisplay {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.houseDisplay1 {
  display: flex;
  flex-direction: column;
}
.house-design1 {
  align-self: flex-start;
}
.house-design2{
  align-self: flex-end;
  margin-top: 13%;
}
.house-design {
  width: 100%;
}
.architectDesigns-introduction {
}
.architectDesigns-intro {
  padding: 0;
  margin: 8% 0;
}
.architectDesigns-secondDisplay {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 15px;
}
.footer-sendInquiry {
  width: 100%;
  margin: 8% 0;
}
@media only screen and (min-width: 700px) {
  .architectPortfolio-container {
    width: 40%;
    margin: 0 auto;
  }
}
