.collection-banner {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("../assets/clb.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: black;
  height: 15%;
  padding-top: 3rem;
}
.collection-banner > h3 {
  text-align: center;
  padding-top: 25%;
  padding-bottom: 15%;
  font-size: 1.7rem;
  font-weight: 700;
  color: white;
  justify-items: center;
}
.collection-banner > a {
  padding-top: 20rem;
  color: white;
}
.col-container {
  margin: 0;
  padding: 0 auto;
  padding-bottom: 50px;
  width: 100%;
}

.collection-list {
  width: 100%;
  list-style: none;
}
.collection-list > li:last-child {
  border-bottom: none;
}
.collection-list > li {
  border-bottom: 1px solid gray;
}

.collection-list > li > button {
  width: 100%;
  display: flex;
  padding: 1.5rem 1rem 1.5rem 1rem;
  justify-content: space-between;
  background: none;
  border: none;
}

@media only screen and (min-width: 700px) {
  .col-container {
    width: 40%;
    margin: 0 auto;
  }
}
