.order-item {
  display: flex;
  flex-direction: row;
  font-family: Cabin;
}
.order-item > img {
  height: 8rem;
  margin: 0;
}
.order-item > div {
  text-align: left;
  margin: 1rem 0;
  height: 6rem;
  padding: 0.2rem 0 !important ;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  letter-spacing: 0.03em;
}
.order-item > div > h3 {
  font-size: 12px;
  line-height: 15px;
}
.order-item > div > div > h3 {
  font-size: 14px;
  line-height: 17px;
}
.order-item > div > div > p {
  font-size: 10px;
  line-height: 12px;
  color: rgba(0, 0, 0, 0.4);
}
.prompt {
  font-family: Cabin;
  text-align: left;
  margin: 2rem 0;
  line-height: 22px;
}
.prompt > h3 {
  font-size: 18px;
}
.prompt > p {
  font-size: 14px;
}
.shipping-cost {
  justify-content: space-between;
  margin: 1rem 0;
  font-family: Cabin;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.03em;
}
.button1 {
  background-color: white;
  border: 1px solid rgba(0, 0, 0, 0.651);
}
.button2 {
  background-color: black;
  color: white;
  border: none;
}
.ship-button {
  font-family: Cabin;
  width: 90%;
  align-self: center;
  margin: 1rem auto;
  height: 2.5rem;
}
.recomended {
  text-align: left;
}
.recomended-container {
  margin-bottom: 5.5rem;
}
