@import "~antd/dist/antd.css";
* {
  margin: 0;
  padding: 0;
}

@import url("https://fonts.googleapis.com/css2?family=Cabin:wght@400;500;600;700&family=Cormorant+Garamond:wght@300;400;500;600;700&display=swap");

.payment-container {
  width: 90%;
  margin: 0 auto;
}
.paymentpage-name {
  text-align: center;
  margin: 5% 0 1%;
  color: rgba(0, 0, 0, 1);
  font-family: "Cormorant Garamond", serif;
  font-size: 18px;
  font-weight: 700;
}
.billing-info {
  width: 100%;
  margin: 8% 0 5%;
}
.billing-header,
.billing-address {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.billing,
.customer-name {
  color: rgba(0, 0, 0, 1);
  font-family: "Cabin", sans-serif;
  font-size: 14px;
  font-weight: 400;
}
.edit {
  text-decoration: underline;
  font-family: "Cabin", sans-serif;
  font-size: 10px;
  font-weight: #000000;
}
.billing-address {
  width: 45%;
}
.billinginfo-input {
  display: block;
  border: transparent;
  background-color: transparent;
  font-family: "Cabin", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}
.shipping-line {
  display: block;
  margin: 0 -6%;
  border: 20px solid rgba(196, 196, 196, 0.25);
}
.payment-type {
  margin: 5% 0;
  width: 100%;
}
.pay-btn {
  position: relative;
  display: block;
  text-align: start;
  width: 100%;
  padding: 10px 50px;
  margin: 5% 0;
  font-family: "Cabin", sans-serif;
  font-size: 12px;
  font-weight: 400;
  background-color: transparent;
  border: 0.5px solid #000000;
}
.paymenthome-icon {
  position: absolute;
  top: 15%;
  left: 6%;
}
.totalorder-details {
  margin: 4% 0 5%;
}
.ship-info {
  display: flex;
  justify-content: space-between;
}
.ship,
.sub-total,
.subtotal-price {
  margin: 2% 0 0;
  font-family: "Cabin", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #000000;
}
.payment-line {
  width: 100%;
  display: block;
  border: 0.1px solid rgba(0, 0, 0, 0.2);
}
.total,
.total-price {
  margin: 3% 0 0;
  font-family: "Cabin", sans-serif;
  font-size: 18px;
  font-weight: 400;
  color: #000000;
}
.place-order {
  font-family: "Cabin", sans-serif;
  font-size: 12px;
  font-weight: 700;
  margin: 5% 0;
  width: 100%;
  color: rgba(255, 255, 255, 1);
  padding: 10px;
  background-color: #000000;
}

@media only screen and (min-width: 700px) {
  .payment-container {
    width: 40%;
    margin: 0 auto;
  }
}
