.shipping-header {
  justify-content: center;
  margin-bottom: 2rem;
}
.shipping-header > p {
  text-align: center;
  align-self: center;
  margin: 0 auto;
}
.shipping-options {
  display: flex;
  flex-direction: column;
}
.shipping-options > p {
  align-self: flex-start;
}
.shipping-options > div {
  display: flex;
  border-bottom: 1px solid grey;
  padding: 0.5rem 0;
}
.shipping-type {
  margin: 1rem 0;
}
.shipping-options > div > div {
  align-items: flex-start;
  padding-left: 1rem;
  display: flex;
  flex-direction: column;
  width: 80%;
}
.shipping-options > div > div > * {
  margin: 0.2rem;
}
.shipping-info {
  border: 1px solid black;
  height: 3rem;
  margin: 1rem 0;
  padding-left: 1.5rem;
}
.button-container > a > button {
  width: 90%;
  margin: 1rem auto 6rem auto;
}
.sb2 {
  height: 4rem;
  margin: 1rem 0;
}
