.design-header {
  display: flex;
  flex-direction: row;
  width: 100%;
  text-align: center;
  justify-content: space-between;
  padding: 2rem 0.5rem 2rem 0.5rem;
  border-bottom: 1px solid rgb(158, 153, 153);
  font-size: 1.5rem;
  font-weight: 600;
  font-family: Cormorant Garamond;
}
.design-header > a {
  align-self: center;
}
.designers {
  margin-left: auto;
  margin-right: auto;
  font-family: Cormorant Garamond;
  justify-content: center;
}
.designTag {
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  text-align: center;
  margin: 1rem auto 1rem auto;
  padding: 0 40px 0 40px;
}
.designWrapper {
  border-bottom: 1px solid rgb(158, 153, 153);
  margin-bottom: 2rem;
  padding-bottom: 2rem;
}
.designWrapper:last-child {
  border-bottom: none;
}
.designCard {
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}
.designCard > p {
  margin: -1rem 1rem 0 1rem;
  font-family: Cabin;
  font-weight: 100;
  font-size: 0.7rem;
}
.designCard > p > a {
  color: black;
  text-decoration: underline;
}
.designImage {
  display: block;
  margin-left: auto;
  margin-right: auto;
  filter: brightness(50%);
}
.designMid {
  display: flex;
  justify-content: space-between;
  font-weight: 400;
  font-style: Regular;
  font-size: 12px;
  margin: 1rem 4% 1rem 4%;
}
.likes > button {
  background-color: rgba(255, 255, 255, 0);
  border: none;
}
