.product-container {
  display: flex;
  flex-direction: column;
}
.product-container > button {
  margin: 0 auto;
  width: 90%;
}
.no-notif {
  display: none;
}
.added-notification {
  font-family: comorant garamond;
  position: fixed;
  height: 6.5rem;
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  top: 0;
  padding: 3.5rem 1rem 0 1rem;
  width: 100%;
  background-color: black;
  animation: modal 0.2s;
}
@keyframes modal {
  from {
    height: 2%;
  }
  to {
    height: 6.5rem;
  }
}
.added-notification > a {
  width: 35%;
}
.added-notification > a > button {
  justify-self: flex-end;
  width: 100%;
  height: 1.7rem;
  background-color: white;
  color: black;
  border: none;
  float: right;
}
.product-header {
  border-bottom: none;
  padding-bottom: 0;
}
.product-header > button {
  border: none;
  background-color: #ffffff00;
}
.product-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 18rem;
  margin: 0 auto;
}
.section-border {
  color: 1px solid rgb(158, 153, 153);
  margin: 2rem 0 1rem 0;
}
/*  */
.price {
  margin: 1rem auto 0 auto;
  font-family: Cabin;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  color: #00000066;
}
.share-container {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  margin-left: 25%;
}
.product-image {
  margin: -1rem;
  position: relative;
}
.product-variations {
  display: flex;
  width: 110%;
  justify-content: space-between;
}
.product-variations > select {
  -webkit-appearance: none;
  border-radius: 10px;
  width: 45%;
  display: flex;
  box-sizing: border-box;
  height: 40px;
  font-family: Cabin;
  font-weight: lighter;
  margin: 0 0.8rem;
  font-size: 0.9rem;
  padding-left: 12%;
  background-color: #ffffff00;
}
.section-border-2 {
  height: 2rem;
  background-color: rgba(196, 196, 196, 0.25);
  margin: 2rem 0;
}
.product-info {
  font-family: Cabin;
}
.product-info > li {
  border-bottom: 1px solid rgba(163, 163, 163, 0.966);
}
.product-info > li:last-child {
  border-bottom: none;
  margin-bottom: 5rem;
}
.product-info > li > button {
  background: #ffffff00;
  border: none;
  justify-content: space-between;
  display: flex;
  width: 90%;
  margin: 1.5rem auto;
  font-size: 0.8rem;
  align-self: center;
  color: black;
}
.product-info > li > button > a {
  color: black;
}
.product-info-display {
  width: 80%;
  margin: 0.5rem auto;
}
.product-info-nodisplay {
  width: 80%;
  margin: 0.5rem auto;
  display: none;
}
