.homeScreen {
  font-family: Cormorant Garamond;
}
.flashPrompt {
  font-family: Cabin;
  font-weight: bold;
  font-size: 12px;
  text-align: center;
  color: white;
  background-color: black;
  height: 30px;
  padding-top: 0.6rem;
}
#red {
  color: #f60a0a;
}
.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: 3px solid #f4f4f4;
}
.card > p {
  margin: 5px 0 5px 0;
  font-family: Cabin;
}
.card1 > p {
  font-size: 10px;
}
.cardTitle {
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  display: flex;
  align-items: center;
  text-align: center;
  padding: 0 40px 0 40px;
  margin: 1rem 0 1rem 0;
}
.card1Img {
  width: 100%;
}
.card2 {
  border-bottom: 3px solid #f4f4f4;
}
.card2 > p {
  padding: 0 0.6rem 0 0.6rem;
  font-family: Cabin;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  margin: 0.6rem 0 0.6rem 0;
}
.card2Banner {
  display: flex;
  justify-content: space-between;
  padding: 0 0.6rem 0 0.6rem;
  width: auto;
  margin: 0 0 -0.6rem 0;
}
.card2Banner > p {
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
}
.card2Banner > a {
  align-self: center;
  float: right;
  font-family: Cabin;
  font-weight: 600;
  font-size: 10px;
  color: black;
  letter-spacing: 1px;
}
.likeIcon {
  align-self: flex-end;
  position: relative;
  top: 20px;
  right: 20px;
}
.itemCard {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #f4f4f4;
  border-radius: 20px;
  margin: 5px;
  height: auto;
}
.itemCard > * {
  margin: 0 0 5px 0;
}
.itemImage {
  padding: 0 30px 0 30px;
}
.itemName {
  font-weight: 600;
  font-size: 15px;
  color: #898a8d;
}

.itemPrice {
  font-family: Cabin;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 17px;
}
.horizontalScroll {
  overflow-x: scroll;
  display: flex;
  flex-direction: row;
}
.card3Title {
  padding: 1rem 0.6rem 1rem 0.6rem;
  text-align: left;
}
.card4 {
  text-align: center;
}
.card4 > a {
  font-size: 15px;
  align-self: center;
  font-family: Cabin;
  color: black;
  letter-spacing: 1px;
  padding: 1rem 0 1rem 0;
  word-spacing: 1px;
  padding-top: 2rem;
}
.buttonStyle {
  background-color: black;
  color: white;
  border: 0;
  cursor: pointer;
  font-weight: 1000;
  margin: 20px 0 20px 0;
  font-family: Cabin;
  padding: 10px 40px;
  height: 40px;
}