@import "~antd/dist/antd.css";
* {
  margin: 0;
  padding: 0;
}
@import url("https://fonts.googleapis.com/css2?family=Cabin:wght@400;500;700&family=Cormorant+Garamond:wght@300;400;500;700&display=swap");

.allproducts-container {
  width: 90%;
  margin: 0 auto;
}
.header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  position: relative;
  margin: 10% 0 5%;
  width: 100%;
}
.allproductspage-name {
  font-family: "Cormorant Garamond", serif;
  font-size: 18px;
  font-weight: 700;
  margin: 0;
}
.allproductsfilter-container {
  background-color: transparent;
  margin: 3% 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.sort,
.filter {
  padding: 3% 12%;
  border-radius: 10px;
  background-color: transparent;
  border: 0.5px solid #000000;
  font-size: 10px;
  font-family: "Cabin", sans-serif;
  font-weight: 400;
}
.products-container {
  margin: 5% 0 25%;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 15px;
  overflow: hidden;
}
.product-card {
  width: 100%;
  background-color: rgba(244, 244, 244, 1);
  border-radius: 18px;
  border: transparent;
  text-align: center;
  overflow-x: hidden;
  position: relative;
}
.allproduct-image {
  width: 100%;
  display: block;
  align-self: center;
}
.product-name {
  margin: 0;
  font-family: "Cormorant Garamond", serif;
  font-size: 13px;
  font-weight: 600;
  line-height: 15px;
  text-transform: capitalize;
  color: black;
}
.product-price {
  margin: 2% 0 5%;
  font-weight: 400;
  line-height: 15px;
  color: rgba(0, 0, 0, 0.4);
  font-size: 13px;
  font-family: "Cabin", sans-serif;
}
.product-borderline {
  display: block;
  margin: 0 -6%;
  border: 0.2px solid rgba(0, 0, 0, 0.2);
}
.liked {
  position: absolute;
  top: 5%;
  right: 10%;
  overflow: hidden;
}

@media only screen and (min-width: 700px) {
  .allproducts-container {
    width: 40%;
    margin: 0 auto;
  }
}
