.d-profile-header {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.d-back-arrow {
  justify-self: center;
  padding-top: 1rem;
}
.designer-name {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  padding-right: 2%;
  font-size: 1.2rem;
}
.profile-button {
  width: 80%;
}
.like-profile {
  height: 2rem;
}
.designer-profile {
  display: flex;
  padding: 0.5rem;
  height: 100%;
}
.designer-profile > p {
  height: 80%;
  font-size: 80%;
  text-align: left;
}
.designer-profile > img {
  padding-right: 5%;
  height: 100%;
}
.word-banner {
  padding: 2rem 1.5rem 2rem 1.5rem;
  background-color: #c4c4c44f;
  margin-left: -10%;
  margin-right: -10%;
  font-size: 1.5rem;
  font-weight: 600;
}
.image-grid {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 1rem auto 1rem auto;
}
.grid-col-1 {
  justify-content: space-between;
  display: flex;
  flex-direction: column;
  width: 45%;
}
.grid-col-1 > img {
  height: 45%;
}
.grid-col-2 {
  width: 45%;
}
.grid-col-2 > img {
  height: 100%;
  width: 100%;
}
.mid-section {
  text-align: left;
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.foot-button > button {
  width: 100%;
}
.grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 15px;
  margin-left: auto;
  margin-right: auto;
  justify-items: center;
}
.likeButton {
  border: none;
  background-color: rgba(255, 255, 255, 0);
  margin-left: 10%;
}
