/*sign up style*/
.signHeader {
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 20px;
  align-self: flex-start;
  margin-left: 10px;
}
.signup-back-arrow {
  margin-left: 10px;
}
.formWrapper {
  font-family: Cormorant Garamond;
  padding-top: 20px;
  display: flex;
  width: 100%;
  justify-content: center;
  border-top: 1.5px solid gray;
}
.signUpForm {
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 10px;
  align-self: center;
}
.formText,
.wrongInput,
.rightInput {
  border: 1px solid !important;
  box-sizing: border-box;
  font-family: Cormorant Garamond;
  width: 20rem;
  height: 43px;
  align-self: center;
  margin: 0.7rem;
  font-size: 1.1rem;
}
.formText ::placeholder {
  font-size: 1.1rem;
}
.wrongInput {
  border: 1.5px solid red !important;
}
.rightInput {
  border: 1px solid green !important;
}
.formSubmit {
  background-color: black;
  color: white;
  border: 0;
  cursor: pointer;
  font-weight: 1000;
  margin-top: 30px;
}
.noSubmit {
  background-color: rgb(61, 61, 61);
  color: white;
  border: 0;
  font-weight: 1000;
  margin-top: 30px;
}
.policy {
  font-size: 0.6rem;
  font-family: cabin;
  align-self: center;
}
.prompt {
  font-size: 1rem;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  align-self: flex-start;
  margin: -10px 0px 15px 10px;
}
#prompt1 {
  margin-bottom: 0.6rem;
  margin-top: 1rem;
  font-size: 1rem;
}
.signUpForm > button {
  width: 20rem;
  height: 43px;
  font-family: cabin;
  align-self: center;
  margin: 0.7rem;
  font-size: 1.1rem;
}

.signUpForm > p {
  margin: 0.6rem 0 0 0.6rem;
  font-size: 15px;
}
input:focus,
select:focus,
textarea.formText:focus,
button:focus {
  outline: none;
}

/*sign up style*/
