.planningPage {
  font-family: Cormorant Garamond;
}
.planningPage > section {
  padding: 1rem 0.6rem 0 0.6rem;
  margin: auto;
  font-size: 1.2rem;
  font-weight: 600;
  width: 20rem;
}
.planBanner {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("../assets/fteR0e2BzKo.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: black;
  height: 15%;
  padding-top: 3rem;
}

.planBanner > h3 {
  text-align: center;
  padding-top: 25%;
  padding-bottom: 15%;
  font-size: 1.7rem;
  font-weight: 700;
  color: white;
  justify-items: center;
}
.planBanner > a {
  padding-top: 20rem;
  color: white;
}
.planningForm {
  display: flex;
  flex-direction: column;
  padding: 2rem 0.6rem 2rem 0.6rem;
  align-self: center;
  align-items: center;
}
.planningForm > input,
.planningForm > select {
  border: 1px solid black;
  box-sizing: border-box;
  width: 20rem;
  height: 43px;
  align-self: center;
  background-color: #ffffff00;
  margin: 0.8rem;
  font-size: 1.1rem;
  padding-left: 1rem;
  background-color: #ffffff00;
}
.planningForm > button {
  width: 20rem;
}
.subSection {
  width: 20rem;
  display: flex;
  padding-right: 0rem;
}
.subSection > select {
  margin-right: 0;
  border: 1px solid black;
  background-color: #ffffff00;
}
.subSection > input {
  border: 1px solid black;
  box-sizing: border-box;
  height: 43px;
  width: 50rem;
  align-self: center;
  font-size: 1.1rem;
  padding-left: 1rem;
}
.subSection > label {
  font-size: 0.8rem;
  white-space: nowrap;
  font-weight: 700;
  padding-right: 0.5rem;
  margin-top: 0.3rem;
}
.subSection > textarea {
  height: 7rem;
  width: 20rem;
  border: 1px solid black;
  margin-top: 0.8rem;
}
.text-section {
  flex-direction: column;
}
.hide-modal {
  display: none;
}
.planning-modal {
  bottom: 0rem;
  background-color: rgb(182, 182, 182);
  position: fixed;
  height: 40vh;
  border-radius: 2rem 2rem 0 0;
  animation: modal 0.5s;
}
@keyframes modal {
  from {
    height: 2%;
  }
  to {
    height: 40vh;
  }
}
.planning-modal > button {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  float: right;
  margin-top: 10px;
  margin-right: 20px;
}
.planning-modal > button > a > img {
  width: 1.5rem;
  height: 1.5rem;
  border: none;
  border-radius: 10px;
  font-weight: 600;
}
.planning-modal > h2 {
  text-align: center;
  justify-self: center;
  font-family: Cabin;
  font-weight: 600;
  padding: 10% 10%;
  color: rgb(31, 31, 31);
  font-size: 2rem;
}
