@import "~antd/dist/antd.css";
* {
  margin: 0;
  padding: 0;
}
@import url('https://fonts.googleapis.com/css2?family=Cabin:wght@400;500;600;700&family=Cormorant+Garamond:wght@300;400;500;600;700&display=swap');
.container {
  width: 90%;
  margin: 0 auto;
}
.feature-form{
  width: 100%;
  position: relative;
}
.search-icon{
  position: absolute;
  top: 50%;
  left: 14%;
}
.search-input {
  width: 100%;
  padding: 10px;
  border-radius: 10px;
  background-color: rgba(218, 218, 218, 0.2);
  border: transparent;
  font-size: 14px;
  text-align: center;
  margin: 10% 0 5%;
  font-family: "Cabin", sans-serif;
  font-weight: 400;
  color: black;
}
.search-input::placeholder {
  font-family: "Cabin", sans-serif;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.44);
  font-size: 14px;
}
.bmh-features-headers {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 0;
}
.bmh-feature {
  border: transparent;
  background-color: transparent;
  font-family: "Cormorant Garamond", serif;
  font-weight: 400;
  font-size: 18px;
  color: rgba(0, 0, 0, 1);
}
.feature1-header:hover {
  border-bottom: 1px solid rgba(0, 0, 0, 1);
  cursor: pointer;
}
.line {
  display: block;
  margin: 0 -6%;
  border: 0.2px solid rgba(0, 0, 0, 0.2);
}
.buildit-features {
  margin: 10% 0 25%;
}
.feature {
  border: transparent;
  width: 100%;
  margin: 5% 0;
  position: relative;
}
.service-name{
  font-family: "Cormorant Garamond", serif;
  font-weight: 700;
  font-size: 20px;
  margin: 0;
  color: rgba(255, 255, 255, 1);
  position: absolute;
  top: 30%;
  left: 10%;
  text-transform: capitalize;
}
.service{
  width: 100%;
  filter: brightness(50%);
  margin-bottom: 5%;
}
.footer{
  width: 100%;
}


@media only screen and (min-width: 700px) {
  .container {
    width: 40%;
    margin: 0 auto;
  }
}
