@import '~antd/dist/antd.css';
* {
  margin: 0;
  padding: 0;
}
@import url("https://fonts.googleapis.com/css2?family=Cabin:wght@700&family=Cormorant+Garamond:wght@500;700&display=swap");

.container {
  width: 90%;
  margin: 0 auto;
  text-align: center;
  font-family: "Cormorant Garamond", serif;
  font-weight: 700;
}

h1 {
  font-weight: 700;
  font-size: 40px;
}
.page-name-header {
  margin: 10% 0 0;
  display: flex;
  justify-content: space-between;
  width: 60%;
}
.page-name {
  margin: 2%;
}
.line {
  display: block;
  margin: 0 -6%;
  border: 1px solid rgba(0, 0, 0, 0.2);
}
.signin-form {
  width: 100%;
  text-align: start;
  margin-top: 10%;
}
.signin-text {
  margin: 10px 0 15px;
  font-weight: 500;
  font-size: 14px;
}
.input {
  width: 100%;
  margin: 3% 0;
  border: 1px solid black;
  padding: 10px;
  border-radius: 0;
}
.input::placeholder {
  font-weight: 500;
}
.signin-btn {
  width: 100%;
  background-color: black;
  border: transparent;
  border-radius: 0%;
  padding: 10px;
  color: white;
  margin: 5% 0;
}
.forgotpassword {
  color: black;
  text-decoration: none;
  font-family: "Cabin", sans-serif;
  font-weight: 700;
}
.signin-password-match,
.correct-password-length,
.no-password-errormessage {
  display: none;
}
.signin-password-unmatch,
.incorrect-password-length,
.password-errormessage {
  display: block;
  color: red;
}

@media only screen and (min-width:700px){
  .container{
    width: 40%;
    margin: 0 auto;
  }
}
