@import '~antd/dist/antd.css';

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
@import url('https://fonts.googleapis.com/css2?family=Cabin:wght@700&family=Cormorant+Garamond:wght@500;700&display=swap');


.welcome-container{
    width: 100%;
    text-align: center;
    font-family: 'Cormorant Garamond', serif;
    font-weight: 500;
    margin-bottom: 5%;
}
.container__image{
    width: 100%;
}
.welcome-form{
    width: 80%;
    margin: 10% auto 3%;
}
.mainwelcome-input{
    width: 100%;
    padding: 10px;
    border-radius: 0;
    border: 1px solid black;
}
.container__btn{
    margin: 5% 0 0 0;
    width: 100%;
    padding: 10px;
    text-align: center;
    background-color: rgba(0, 0, 0, 1);
    color: rgba(255, 255, 255, 1);
    font-weight: 700;
    border: transparent;
    border-radius: 0;
}
.welcome-text{
    margin: 1% 0 15%;
    color: black;
    text-decoration: none;
}
.right-email-format{
    display: none;
}
.wrong-email-format{
    display: block;
    color: red;
}

@media only screen and (min-width:700px){
    .welcome-container{
      width: 40%;
      margin: 0 auto;
    }
  }