@import "~antd/dist/antd.css";
* {
  margin: 0;
  padding: 0;
}
@import url('https://fonts.googleapis.com/css2?family=Cabin:wght@400;500;700&family=Cormorant+Garamond:wght@300;400;500;700&display=swap');

.architeuralDesign-container {
  width: 90%;
  margin: 0 auto;
  text-align: center;
}
.architectList-header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 10% 0 0;
}
.page-name {
  font-size: 18px;
  margin: 0;
  font-family: "Cormorant Garamond", serif;
  font-weight: 700;
}

.line,
.design-line {
  display: block;
  margin: 0 -6%;
  border: 0.5px solid rgba(0, 0, 0, 0.2);
}
.designs {
  width: 100%;
  margin: 10% 0 30%;
}
.design-text {
  font-family: "Cormorant Garamond", serif;
  font-weight: 700;
  font-size: 20px;
  padding: 0 5%;
}
.design {
  width: 100%;
}
.design-image {
  width: 100%;
  display: block;
  height: 200px;
  filter: brightness(40%);
}
.design-description {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 1% 0;
}
.design-name {
  font-size: 12px;
  font-weight: 400;
  font-family: "Cormorant Garamond", serif;
  margin: 0;
}
.numberOfLikes,
.architect {
  font-size: 10px;
  font-family: "Cabin", sans-serif;
  font-weight: 400;
}
.architect {
  text-align: start;
  margin: 0;
}
.architect-name {
  text-decoration: underline;
  border: transparent;
  background-color: transparent;
  margin-left: 1%;
  color: black;
}
.architect-name:hover {
  text-decoration: underline;
}
.design-line {
  margin: 8% -6%;
}
.footer {
  background-color: rgba(250, 250, 250, 1);
}

@media only screen and (min-width: 700px) {
  .architeuralDesign-container {
    width: 40%;
    margin: 0 auto;
  }
}
