@import "~antd/dist/antd.css";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
@import url("https://fonts.googleapis.com/css2?family=Cabin:wght@700&family=Cormorant+Garamond:wght@500;700&display=swap");

.footer-nav {
  /* width: 90%; */
  background-color: rgba(250, 250, 250, 1);
  position: fixed;
  margin: 0 auto;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 10px 5%;
  display: flex;
  justify-content: space-between;
  font-family: "Cabin", sans-serif;
  font-weight: 400;
  font-size: 10px;
}
.icon-name {
  color: #898a8d;
}
@media only screen and (min-width: 700px) {
  .footer-nav {
    width: 50%;
    margin: 0 auto;
  }
}
