.loading {
  text-align: center;
  align-items: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  align-self: center;
  margin-top: 70%;
  width: 50%;
  animation-duration: 3s;
}
.bounce {
  animation-name: bounce;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}
@keyframes bounce {
  0%   { transform: translateY(0); }
  50%  { transform: translateY(25px); }
  100% { transform: translateY(0); }
}

.loading-text {
  margin: 50% 0 20%;
}
